<template>
  <div class="RecruitPage">
  	<HeaderTag page="recruit"></HeaderTag>
	<Focus></Focus>
	<div class="join_us same_top">
		<div class="mini_width">
			<div class="top">
				<h1></h1>
				<p>O2O</p>
				<p></p>
			</div>
			<ul class="cf">
				<li>
					<img src="./img/pig.png" height="120" width="120">
					<h2></h2>
					<p></p>
					<p></p>
				</li>
				<li>
					<img src="./img/arrow.png" height="120" width="120">
					<h2></h2>
					<p></p>
					<p></p>
				</li>
				<li>
					<img src="./img/hand.png" height="120" width="120">
					<h2></h2>
					<p>+</p>
					<p></p>
				</li>
				<li class="last">
					<img src="./img/puzzle.png" height="120" width="120">
					<h2></h2>
					<p></p>
					<p></p>
				</li>
			</ul>
		</div>
	</div> 
	<div class="flow same_top">
		<div class="mini_width">
			<div class="top">
				<h1></h1>
				<p>O2O</p>
			</div>
			<div class="qr">
				<div class="step"></div>
				<h1>APP</h1>
				<p></p>
				<div class="qrpic"></div>
				<p></p>
				<p>400-800-8820</p>
			</div>
		</div>
	</div> 
	<FooterTag></FooterTag>
  </div>
</template>

<script>
import HeaderTag from '../common/Header.vue'
import Focus from '../common/Focus.vue'
import FooterTag from '../common/Footer.vue'
export default {
	name: 'RecruitPage',
	data() {
		return {
			msg: 'Welcome to Your Vue.js App'
		}
	},
	mounted: function() {
		var curNum = 0;
		var curNum2 = 0;
		var liNums = $(".bannerbox .banner").length - 1;
		$(".bannerbox .banner").eq(0).css("left", "50%");
		$(".bannerbox").hover(function() {
			$(".leftB,.rightB").css("display", "block");
		}, function() {
			$(".leftB,.rightB").css("display", "none");
		});
		$(".leftB").click(function() {
			$(".banner").finish();
			var temp = curNum;
			curNum--;
			if (curNum == -1) {
				curNum = liNums;
			}
			$(".banner").eq(curNum).css("left", "150%").animate({
				left: "50%"
			});
			$(".banner").eq(temp).animate({
				left: "-150%"
			});
			$(".focusBox li").eq(curNum).addClass("cur").siblings().removeClass("cur");
		});
		$(".rightB").click(function() {
			$(".banner").finish();
			var temp = curNum;
			curNum++;
			if (curNum == $(".bannerbox .banner").length) {
				curNum = 0;
			}
			$(".banner").eq(curNum).css("left", "-50%").animate({
				left: "50%"
			});
			$(".banner").eq(temp).animate({
				left: "250%"
			});
			$(".focusBox li").eq(curNum).addClass("cur").siblings().removeClass("cur");
		});

		function autoScroll() {
			$(".banner").finish();
			var temp = curNum;
			curNum--;
			if (curNum == -1) {
				curNum = liNums;
			}
			$(".banner").eq(curNum).css("left", "150%").animate({
				left: "50%"
			});
			$(".banner").eq(temp).animate({
				left: "-150%"
			});
			$(".focusBox li").eq(curNum).addClass("cur").siblings().removeClass("cur");
		};
		var clearIn = setInterval(autoScroll, 3000);
		$(".bannerbox").hover(function() {
			clearInterval(clearIn);
		}, function() {
			clearIn = setInterval(autoScroll, 3000);
		});
		$(".focusBox li").hover(function() {
			$(".banner").finish();
			$(".focusBox li").eq($(this).index()).addClass("cur").siblings().removeClass("cur");
			curNum2 = $(this).index();
			if (curNum == curNum2) {
				return;
			} else if (curNum < curNum2) {
				$(".banner").eq(curNum2).css("left", "150%").animate({
					left: "50%"
				});
				$(".banner").eq(curNum).animate({
					left: "-150%"
				});
			} else if (curNum > curNum2) {
				$(".banner").eq(curNum2).css("left", "-50%").animate({
					left: "50%"
				});
				$(".banner").eq(curNum).animate({
					left: "250%"
				});
			}
			curNum = curNum2;
			curNum2 = "";
		}, function() {

		})
	},
  	components: {
    	Focus,HeaderTag,FooterTag
  	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.join_us{
		height: 440px;
		background-color: #f6f6f6;
	}
	.same_top .mini_width{
		width: 1200px;
		margin:0 auto;
	}
	.same_top .top h1{
		font-family: 'Microsoft YaHei';
		font-size: 24px;
		line-height: 24px;
		padding-top: 20px;
		padding-bottom: 10px;
		color: #333;
		text-align: center;
	}
	.same_top .top p{
		font-family: 'Microsoft YaHei';
		font-size: 16px;
		line-height: 35px;
		color: #666;
		text-align: center;
	}
	.join_us ul{
		margin-top: 30px;
		margin-left:10px;
	}
	.join_us ul li{
		float: left;
		width: 278px;
		height: 239px;
		margin-right: 20px;
		border: 1px solid #ddd;
		background-color: #fff;
	}
	.join_us ul li.last{
		margin-right: 0;
	}
	.join_us ul li img{
		display: block;
		margin: 10px auto;
	}
	.join_us ul li h2{
		font-family: 'Microsoft YaHei';
		font-size: 20px;
		padding-bottom: 10px;
		color: #333;
		text-align: center;
	}
	.join_us ul li p{
		font-family: 'Microsoft YaHei';
		font-size: 14px;
		line-height: 20px;
		color: #666;
		text-align: center;
	}
	.flow{
		height: 570px;
		background-color: #fff;
	}
	.flow .qr .step{
		width: 1180px;
		height: 47px;
		margin:40px 10px 20px 10px;
		background: url(img/flow.png) no-repeat;
	}
	.flow .qr h1{
		font-family: 'Microsoft YaHei';
		font-size: 24px;
		padding-bottom: 10px;
		color: #333;
		text-align: center;
	}
	.flow .qr p{
		font-family: 'Microsoft YaHei';
		font-size: 16px;
		color: #333;
		text-align: center;
	}
	.flow .qr .qrpic{
		width: 200px;
		height: 200px;
		margin: 10px auto 10px;
		background: url(img/qrpic.png) no-repeat;
	}
</style>



